.expenses-list-container{
    margin-top: 30px;
}

.expenses-list-container > strong{
    font-weight: bold;
    font-size: 18px;
    color: #333;
    text-shadow: 1px 1px 0 #999;
}

.expenses-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.expenses-list__fallback {
    color: white;
    text-align: center;
}

.expense-list-dummy{
    color: #333;
    text-shadow: 1px 1px 0 #999;
    font-weight: bold;
}

.expense-list-dummy .expense-item{
    display: flex;
    align-items: center;
    justify-content: center;
}

.expense-item-li.selected > .card{
    position: relative;
}

.expense-item-li.selected > .card::after {
    position: absolute;
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    border: 6px solid #fff;
    top: 0;
    left: 0;
    z-index: 10;
    box-sizing: border-box;
    border-radius: 12px;
    filter: blur(2px);
    background: transparent;
    pointer-events: none;
}