.new-expense {
    padding: 0;
    margin: 0;
    width: 100%;
    max-width: 100%;
    border-radius: 12px;
    text-align: center;

    background-color: #EBECF0;
    box-shadow: -5px -5px 20px #FFF, 5px 5px 20px #BABECC;

    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 9999;
}

